
<template>
    <div>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle  py-0">
                            <button type="button" class="close" v-on:click="CloseCustomFieldsPopup()">
                                <em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                            <h4 name="header" class="modal-title py-3">
                                {{ $t('AddCustomFields') }}
                            </h4>
                        </div>
                        <div class="container-fluid padding-t_10">
                            <div class="left-menu-tab new_tab">
                                <div class="col-md-12 p-0">
                                    <div class="theme-primary partition">
                                        <span class="p-name text-white">User Defined Fields:</span>
                                    </div>
                                </div>
                                <ul class="nav nav-pills flex-column left-tab mt-2 responivecustomtab" id="upper"
                                    role="tablist">
                                    <li class="nav-item" v-for="element in systemFieldList" :key="element">
                                        <a id="leftmenu" class="nav-link left-menu " @click="activateTab(element)"
                                            :class="{ 'active': selectedModule === element }">
                                            <i id="itemdata" v-bind:class="element.class_name"></i>&nbsp;
                                            {{ element.display_name }}
                                        </a>
                                    </li>
                                </ul>
                            </div>
                            <div class="right-content">
                                <div class="col-lg-12 mb-0 float-left p-0">
                                    <div class="heading-border">
                                        <h5 class="h5">
                                            <span class="spngropuname">
                                                {{ $t('Forminfo') }}
                                            </span>
                                        </h5>
                                    </div>
                                </div><br /><br />
                                <Form ref="Propertiesform1">
                                    <div class="listing py-3 px-3 " v-if="showFieldPropPopup">
                                        <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema"
                                            v-on:OnSubmit="onSubmit">
                                        </dynamic-form>
                                    </div>
                                </Form>
                            </div>
                          
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../../services/DataService.js';
// import CustomFieldPropertyPopup from '../Modal/CustomFieldPropertyPopup.vue'
import { DynamicForm } from 'tg-controls_cli/dist/myLib.common'
export default {
    props: {

        LanguageArrayData:
        {
            type: Array,
            required: true
        },
        SubModuleId:
        {
            type: String,
            required: true
        },
        ModuleId: {
            type: String,
            required: true
        }
    },
    components: {
        DataService,
        // CustomFieldPropertyPopup,
        DynamicForm
    },
    data() {
        return {
            drag: false,
            dropfieldslist: [],
            dropfields: [],
            systemFieldsModel: '',
            OldIdForProp: '',
            systemFieldList: [],
            showFieldPropPopup: false,
            FieldName: null,
            DataTypeId: '',
            iSLayoutControl: false,
            divManageLayoutProp: false,
            LanguageArray: [],
            FieldPropertyData: [],
            oldFieldPropertyData: [],
            finalJsonData: [],
            // closepopupMenuhide:false,
            controlOnStart: true,
            bindEditedDataProperty: [],
            disabled: false,
            errorMessage: '',
            classicon: '',
            selectedModule: null,
            langCulture: [],
            myObj: [],
            lenghtOfLanguageDiv: null,
            displayNameData: '',
            customfieldtypeId: null,
            FormSchema: [
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "TextField",
                            label: 'Label',
                            name: "Label",
                            value: this.FieldName,
                            placeholder: "",
                            visibility: true,
                            disabled: false,
                            validationRules: { "required": true },
                            config: {
                                onKeyUp: this.Bindingwithdisplayname
                            }
                        },
                        {
                            astype: "TextAreaField",
                            label: 'Pick List Option',
                            name: "PickListOption",
                            value: '',
                            visibility: false,
                            placeholder: "Enter comma seprated value ",
                            validationRules: { "required": true },
                        },
                        {
                            astype: "NumericField",
                            label: 'Length',
                            name: "Length",
                            value: '',
                            placeholder: "Enter Length",
                            visibility: true,
                            validationRules: { "required": true },
                            config: {
                                onKeyUp: this.LengthLimit
                            }

                        },
                        {
                            astype: "NumericField",
                            label: 'Decimal Places',
                            name: "DecimalPlaces",
                            value: '',
                            placeholder: "",
                            visibility: false,
                            validationRules: { "required": true },

                        },
                        {
                            astype: "CheckBoxField",
                            label: "Mark as Required",
                            name: "MarkasRequired",
                            value: '',
                            placeholder: "",
                            visibility: true,
                            config: {
                                options: [{ value: "1", name: "" }]
                            }
                        },
                        {
                            astype: "TextAreaField",
                            label: 'Description',
                            name: "Description",
                            value: '',
                            placeholder: "Enter Description",
                            visibility: true,
                            validationRules: "",
                            config: {
                                rows: 3
                            }
                        }
                    ],
                },
                {
                    layoutType: "double",
                    Data: [
                        {
                            astype: "SelectField",
                            label: 'Language',
                            name: "Language",
                            value: '',
                            placeholder: "",
                            chkIndex: 0,
                            visibility: false,
                            config: {
                                options: [],

                            },
                            validationRules: "required",
                        },
                        {
                            astype: "TextField",
                            label: 'Display Name',
                            name: "DisplayName",
                            value: '',
                            placeholder: "Enter Display Name",
                            chktxtIndex: 0,
                            visibility: false,
                            validationRules: { "required": true, max: 250 },
                            config: {
                                showAddIcon: true,
                                onAddButtonClick: this.AddMultiLanguage
                                // onKeyUp:isLetter
                                // onKeyUp: this.isLetter
                            },
                        },

                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save"
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.CloseCustomFieldsPopup
                }
            ]

        }
    },
    updated() {
        var vm = this;
        vm.BindLangDDL();
    },
    created: function () {
        var vm = this;
        vm.ShowHideFields();
        vm.GetCustomFieldDataTypes();
        vm.BindLangDDL();
    },
    methods:
    {
        LengthLimit() {
            var vm = this;
            if ((vm.FormSchema)[0].Data[2].value > 250) {
                (vm.FormSchema)[0].Data[2].value = 250; // Set the input value to 250 if it exceeds the limit
         }
        },
        isLetter() {
            var vm = this;
            (vm.FormSchema)[1].Data.forEach(items => {
                if (items.label == "Display Name") {
                    items.value = items.value.replace(/[^a-zA-Z]/g, '');
                }
            })
        },
        lableDisplaynamebinding(value) {
            var vm = this;
            (vm.FormSchema)[1].Data[1].value = value;
        },
        BindLangDDL(name) {
            var vm = this;
            if (name == undefined) {
                vm.LanguageArray.forEach((itm, index) => {
                    (vm.FormSchema)[1].Data[0].config.options.push({
                        value: itm.lang_id,
                        name: itm.lang_name
                    })
                })
            }
            else {
                vm.LanguageArray.forEach((itm, index) => {
                    if (typeof (name) != 'undefined' && name != null) {
                        (vm.FormSchema)[1].Data.forEach((item, indx) => {
                            if (indx % 2 == 0) {
                                (vm.FormSchema)[1].Data[indx].config.options.push({
                                    value: itm.lang_id,
                                    name: itm.lang_name
                                })
                            }
                        })

                    }
                })
            }
        },
        RemoveFile: function (e, field) {
            var vm = this;
            var checkindex = (vm.FormSchema)[1].Data.findIndex(x => x.chktxtIndex === field.chktxtIndex);
            var chechIndex1 = checkindex - 1;
            if (checkindex > -1) {
                (vm.FormSchema)[1].Data.splice(checkindex, 1);
            }
            if (chechIndex1 > -1) {
                (vm.FormSchema)[1].Data.splice(chechIndex1, 1);
            }
        },
        AddMultiLanguage() {
            var vm = this;
            (vm.FormSchema)[1].Data.push({
                astype: "SelectField",
                label: 'Language',
                name: "Language" + (vm.FormSchema)[1].Data.length,
                value: "",
                placeholder: "",
                chkIndex: (vm.FormSchema)[1].Data.length,
                config: {
                    options: [],
                },
                validationRules: "required",
            },
                {
                    astype: "TextField",
                    label: 'Display Name',
                    name: "DisplayName" + (vm.FormSchema)[1].Data.length,
                    value: '',
                    placeholder: "",
                    chktxtIndex: (vm.FormSchema)[1].Data.length,
                    validationRules: { "required": true, max: 250 },
                    config: {
                        showAddIcon: true,
                        onaddIconBackground: 'bg-danger',
                        onAddButtonClick: vm.RemoveFile,
                        addIcon: 'fa fa-trash',
                        onKeyUp: this.isLetter
                    },
                })
            vm.FormSchema[1].Data.forEach((item) => {
                vm.Lblname = item.name
            })
            vm.BindLangDDL(vm.Lblname);
        },
        ShowHideFields() {
            var vm = this;
            if (vm.IsSystemGeneratedField == true) {
                (vm.FormSchema)[0].Data[1].visibility = false;
                (vm.FormSchema)[0].Data[0].disabled = true;
                (vm.FormSchema)[0].Data[4].disabled = true;
                (vm.FormSchema)[0].Data[2].visibility = false;
                (vm.FormSchema)[0].Data[3].visibility = false;
                (vm.FormSchema)[0].Data[5].visibility = false;
                (vm.FormSchema)[1].Data[0].visibility = false;
                (vm.FormSchema)[1].Data[1].visibility = false;
            }
            else {
                (vm.FormSchema)[1].Data[0].visibility = true;
                (vm.FormSchema)[1].Data[1].visibility = true;
                if (vm.FieldName == 'Decimal') {
                    (vm.FormSchema)[0].Data[3].visibility = true;
                    (vm.FormSchema)[0].Data[2].visibility = false;
                }
                if (vm.FieldName == 'Select List' || vm.FieldName == 'Checkbox' || vm.FieldName == 'Radio') {
                    (vm.FormSchema)[0].Data[1].visibility = true;
                    (vm.FormSchema)[0].Data[2].visibility = false;
                }
                if (vm.FieldName == 'Date') {
                    (vm.FormSchema)[0].Data[1].visibility = false;
                    (vm.FormSchema)[0].Data[3].visibility = false;
                    (vm.FormSchema)[0].Data[2].visibility = false;
                }
            }
        },
        resetform() {
            var vm = this;
            (vm.FormSchema)[0].Data[0].value = "";
            (vm.FormSchema)[0].Data[1].value = "";
            (vm.FormSchema)[0].Data[2].value = "";
            (vm.FormSchema)[0].Data[3].value = "";
            (vm.FormSchema)[0].Data[4].value = "";
            (vm.FormSchema)[0].Data[5].value = "";
            (vm.FormSchema)[1].Data[0].value = "";
            (vm.FormSchema)[1].Data[1].value = "";
        },

        activateTab(item) {
            debugger
            var vm = this;
            vm.$refs.Propertiesform1.reset();
            vm.BindLangDDL();
            vm.resetform();
            (vm.FormSchema)[0].Data[0].value = item.Data_type_name;
            if (item.Data_type_name == "Text") {
                (vm.FormSchema)[0].Data[3].visibility = false;
                (vm.FormSchema)[0].Data[1].visibility = false;
                (vm.FormSchema)[0].Data[2].visibility = true;
            }
            else if (item.Data_type_name == 'Decimal') {
                (vm.FormSchema)[0].Data[3].visibility = true;
                (vm.FormSchema)[0].Data[2].visibility = false;
            }
            else if (item.Data_type_name == 'Select List' || item.Data_type_name == 'Checkbox' || item.Data_type_name == 'Radio') {
                (vm.FormSchema)[0].Data[1].visibility = true;
                (vm.FormSchema)[0].Data[2].visibility = false;
            }
            else if (item.Data_type_name == "Date") {
                (vm.FormSchema)[0].Data[1].visibility = false;
                (vm.FormSchema)[0].Data[2].visibility = false;
                (vm.FormSchema)[0].Data[3].visibility = false;
            }
            else if (item.Data_type_name == "Phone" || item.Data_type_name == "Email") {
                (vm.FormSchema)[0].Data[2].visibility = true;
                (vm.FormSchema)[0].Data[1].visibility = false;
                (vm.FormSchema)[0].Data[3].visibility = false;
                (vm.FormSchema)[0].Data[1].visibility = false;
            }

            else if (item.Data_type_name == "Url" || item.Data_type_name == "Text Area"
            ) {
                (vm.FormSchema)[0].Data[1].visibility = false;
                (vm.FormSchema)[0].Data[3].visibility = false;
                (vm.FormSchema)[0].Data[1].visibility = false;
                (vm.FormSchema)[0].Data[2].visibility = true;
            }
            vm.DataTypeId = item.Data_type_id;
            vm.FieldName = item.Data_type_name;
            vm.iSLayoutControl = true;
            vm.divManageLayoutProp = false;
            vm.LanguageArray = vm.LanguageArrayData;
            vm.oldFieldPropertyData = vm.FieldPropertyData;
            vm.showFieldPropPopup = true;
            vm.selectedModule = item;
            vm.lableDisplaynamebinding(item.Data_type_name);
        },

        CloseCustomFieldsPopup() {
            this.$parent.hidePopup();
        },
        endDrag($vent, type) {
        },
        AllowOneElementdrop() {
            var vm = this;
            if (vm.dropfields == null) {
                vm.disabled = false;
            }
            if (vm.dropfields != null) {
                vm.disabled = true;
            }
            else {
                vm.disabled = false;
            }
        },
        clone({ name }) {
            return { name };
        },
        pullFunction() {
            return this.controlOnStart ? "clone" : true;
        },
        closeAddCustomFieldPopup() {
            var vm = this;
            vm.$parent.closeAddCustomField();
        },
        RemoveField: function (id) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Report")), function () {
                var checkindex = vm.dropfieldslist.findIndex(x => x.Data_type_id === id);
                if (checkindex > -1) {
                    vm.systemFieldList.push(vm.dropfieldslist[checkindex]);
                    vm.dropfieldslist.splice(checkindex, 1);
                    vm.FieldPropertyData = [];
                    if (vm.dropfields == '') {
                        vm.disabled = false;
                    }
                }

            });
        },

        onSubmit(formfields, e) {
            debugger;
            var vm = this;
            var dropdowndata = '';
            var totalLength = this.FormSchema[1].Data.length;
            this.FormSchema[1].Data.forEach((item, index) => {
                if (item.astype != 'TextField') {
                    dropdowndata += item.value + ','
                } else {
                    if (totalLength - 1 != index) {
                        dropdowndata += item.value + '^'
                    } else {
                        dropdowndata += item.value
                    }

                }
            })
            let mainObj = {
                CUSTOM_FIELD_ID: 0,
                MODULE_ID: vm.ModuleId,
                SUB_MODULE_ID: vm.SubModuleId,
                NAME: formfields.Label,
                DISPLAY_NAME: formfields.DisplayName,
                DESCRIPTION: formfields.Description,
                PICKLIST_OPTION: formfields.PickListOption,
                TYPE_ID: vm.DataTypeId,
                LENGTH: formfields.Length,
                IS_REQUIRED: formfields.MarkasRequired[0] == 'undefined' ? 0 : 1,
                STATUSID: 1001,
                DECIMAL_PLACES: formfields.DecimalPlaces,
                LOC_VALUE: dropdowndata,
                FORM_FIELD_VISIBILITY: 'YES',
                LIST_FIELD_VISIBILITY: 'YES'
            }
            vm.finalJsonData.push(mainObj);
            var params = `jsonString=${JSON.stringify(vm.finalJsonData)}`
            DataService.SaveUpdateCustomField(params).then(function (response) {
                var checkstatus = JSON.parse(response.data)
                if (checkstatus.STATUS_CODE == 1) {
                    vm.ShowAlert(vm.$t('CustomFieldSuccess', [vm.$t('Field')]), "success", true, vm.$t("Alert"));
                    vm.$emit('GetCustomNewField', vm.finalJsonData,);
                    vm.CloseCustomFieldsPopup();
                }
                else if (checkstatus.STATUS_CODE == 3) {
                    vm.ShowAlert(vm.$t('CustomFieldExists'), "danger", true, vm.$t("Alert"));
                }
                else {
                    vm.ShowAlert(vm.$t('CustomFieldUpdate'), "success", true, vm.$t("Alert"));
                    vm.$emit('GetCustomNewField', vm.finalJsonData,);
                    vm.CloseCustomFieldsPopup();
                }
            })
        },
        GetCustomFieldDataTypes() {
            var vm = this;
            DataService.GetCustomFieldDataType().then(function (response) {
                if (JSON.parse(response.data) != null) {
                    vm.systemFieldList = JSON.parse(response.data).data;
                    vm.systemFieldList.forEach((item) => {
                    });
                }
            })
        },
        Bindingwithdisplayname(e) {
            var vm = this;
            (vm.FormSchema)[1].Data[1].value = e.target._value;
        }
    }
}
</script>
<style>
.makepopleft#mainDivProp .dropdown-menu {
    inset: unset !important;
    transform: unset !important;
    right: 0px !important;
}
</style>