<template lang="en">
<div class="my-popups">
    <div class="modal d-block ">
        <div class="modal-dialog modal-dialog-centered ui-draggable">
            <div class="modal-content">
                <div class="modal-header ui-draggable-handle  py-0">
                    <button type="button" class="close" @click="closePopup">
                        <em aria-hidden="true" class="fa fa-times"></em>
                    </button>
                    <h4 name="header" class="modal-title py-3">
                        {{ $t('CustomFieldDependency') }} {{displayname}}
                    </h4>
                </div>
                <div class="listing py-3 px-3">
                    <dynamic-form lang="en" ref="DependencyForm" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit">
                     <template v-slot:tgslot-ProductTypeCode="{ data }" v-if="DependencyData"  >
                            <div class="col-lg-12 p-0 workdiary_sanp_row border p-3"  >
                                <div class="timeline-panel row">
                                    <div class="col-md-6" >
                                        <ul class="list-group">
                                            <li class="list-group-item" v-for="(item, index) in GetMultiSelectValue" :key="index" @click="showWhenSelectItem($event,item)" :class="{ sel: selectedItem && selectedItem.name === item.name }">
                                                {{ item.name }}
                                            </li>
                                        </ul>
                                    </div>
                                    <div class="col-md-6 border-left pl-5"  >                                     
                                        <div class="scroldv">
                                            <div class="d-flex" v-if="DependencyData">
                                            <h6 class="col-md-6" >values</h6>   <h6 class="col-md-6" >Isdefault</h6>
                                            </div>
                                            <ul class="list-group " id="rightList">
                                                <li class="list-group-item d-flex" v-for="(itm,index) in Dependencyvalues">
                                                    <label class="form-check form-check-inline col-md-6">
                                                        <div class="custom-control custom-checkbox custom-control-inline">
                                                            <input @change="checkedSelected($event, itm)" :disabled="checkboxdisabled" :id="itm.value" type="checkbox" :checked="checkIfselected(itm.value)" class="chkItems custom-control-input" >
                                                            <label :for="itm.value" class="custom-control-label">{{itm.name}}</label>
                                                            <input type="hidden" :value="itm.value">
                                                        </div>
                                                      
                                                    </label>
                                                    <label class="col-md-6"  >
                                                        <div class="customslidercheck" >
                                                            <input id="isTrueCheckDefault" name="taggele" :checked="IsDefaultValue(itm.value)"  @change="toggleSelected($event, itm)" :id="itm.value+'_id'"  type="checkbox"  :disabled="checkIfselected(itm.value) == false" :class="{'form-check-input':true}">                                                            
                                                            <span class="slider round">
                                                                <span class="slider-yes">Yes</span>
                                                                <span class="slider-no">No</span>
                                                            </span>  
                                                        </div>                   
                                                     </label>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </template>    
                    </dynamic-form>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import {
    DynamicForm
} from 'tg-controls_cli/dist/myLib.common';
import DataService from "../../services/DataService.js";

export default {
    components: {
        DynamicForm
    },
    props: {
        CustomFieldId: {
            type: Number,
            required: true
        },
        DependencyData: {
            type: Array,
            required: true
        },
        CustomLayoutId: {
            type: Number,
            required: true
        },
        Dependencyvalues: {
            type: Array,
            required: true
        },
        displayname: {
            type: String,
            required: true
        },
        CustomLayout_Id:{
            type: Number ,
            required: true
        },
        LayoutList:{
            type : Array,
            required: true
        }
    },
    data() {
        return {
            dependentId: null,
            selectedItem: null,
            dependentOnDdlId: '',
            type: 'dependonddl',
            CustomData: [],
            dependentListArray: [],
            CustomArray: [],
            SelectedCustomFieldName: null,
            GetMultiSelectValue: [],
            GetEditDependencyData: [],
            showWhenListSelectedItem: '',
            TagsSelectedArray: [],
            DepartmentData: [],
            FormSchema: [
                {
                layoutType: "Single",
                Data: [{
                    astype: "SelectField",
                    label: this.$t('DependOn'),
                    name: "DependOn",
                    value: "",
                    visibility: true,
                    config: {
                        options: [],
                        onChange: this.GetCustomFieldDDL
                    },
                    validationRules: "required",
                }

                ],
            },
            {
                layoutType: "Single",
                Data: [{
                    astype: "MultiSelectField",
                    label: this.$t('ShowWhen'),
                    name: "ShowWhen",
                    value: '',
                    mode: "tag",
                    placeholder: "",
                    visibility: true,
                    config: {
                        options: [],
                        onChange: this.GetMultiValues,
                        onRemove: this.removeRecord,
                    },
                    //validationRules: "required",
                },

                ]
            },
            {
                layoutType: "Single",
                Data: [{
                            astype: "CheckBoxField",
                            label: "IsRequired",
                            name: "IsRequired",
                            value: '',
                            placeholder: "",
                            visibility: true,
                            disabled:false,
                            config: {
                                options: [{ value: "1", name: "" }]
                            }
                        },]
            },
            {
                layoutType: "Single",
                Data: [{
                    astype: "SlotField",
                    label: '',
                    name: "ProductTypeCode",
                    value: "",
                    placeholder: "",
                    visibility: true,
                    config: {
                        options: [],
                    },
                }]
            },      
            ],
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Save"
            },

            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                onClick: this.closePopup
            }
            ],
            // DependancyAdded:false,
            // disabledtoggel:true
            checkboxdisabled: true,
            isDefault: '',
            isrequired:'',
            isreq:''
        }

    },
    created: function () {
        var vm = this;
        vm.SelectedCustomFieldName = vm.$parent.customFieldName;
        vm.GetCustomFieldDependent();
        vm.editDependency();
        if (vm.Dependencyvalues.length < 1) {
            (vm.FormSchema)[3].Data[0].visibility = false;
            (vm.FormSchema)[1].Data[0].visibility = false
        }
    },
    methods: {
        IsDefaultValue: function (val) {
            let ths = this;
            let returnValue = false;
            if (ths.selectedItem != null && typeof (ths.selectedItem.value) != 'undefined') {
                let selectedValue = ths.dependentListArray.filter((x) => { return x.value == ths.selectedItem.value });
                if (selectedValue.length > 0 && selectedValue[0].isDefault == val) {
                    return true;
                }
            }
            return returnValue;
        },
        checkIfselected: function (val) {
            let ths = this;
            let returnValue = false;
            if (ths.selectedItem != null && typeof (ths.selectedItem.value) != 'undefined') {
                let selectedValue = ths.dependentListArray.filter((x) => { return x.value == ths.selectedItem.value });
                if (selectedValue.length > 0 && selectedValue[0].dependent.includes(val)) {
                    return true;
                }
            }
            return returnValue;
        },
        GetCustomFieldDependent() { 
            var vm = this;
            (vm.FormSchema)[0].Data[0].config.options = [];
            vm.LayoutList.forEach(xy => {
                xy.FieldData.forEach(xz => {
                    if(xz.name.includes("X") && xy.group_display_name.includes("NewGroup")){
                        vm.DependencyData.forEach(data =>{
                          if(data.name.includes("X")){
                            vm.DependencyData.pop(data);
                          }
                        })
                    }
                })
            }) 
            vm.DependencyData.forEach(item => {
                (vm.FormSchema)[0].Data[0].config.options.push({
                    value: item.custom_field_id,
                    name: item.display_name ? item.display_name : vm.$t(item.name)
                })          
            })
        },
        async GetCustomFieldDDL(e, field, dependentId) {
            var vm = this;
            var CustomData = [];
            if (dependentId) {
                vm.dependentOnDdlId = dependentId;
            } else {
                vm.dependentOnDdlId = e.target.value;
            }
            (vm.FormSchema)[1].Data[0].config.options = [];
            var params = `customFieldId=${vm.dependentOnDdlId}&Type=${vm.type}`;
            await DataService.GetCustomFieldShowWhenDDL(params).then((response) => {
                CustomData = JSON.parse(response.data);
                if (CustomData != null) {
                    if(vm.Dependencyvalues.length > 1){
                        vm.DependencyData=true;
                        (vm.FormSchema)[3].Data[0].visibility = true;
                        (vm.FormSchema)[1].Data[0].visibility = true
                    }
                    else{
                        vm.DependencyData=false;
                        (vm.FormSchema)[2].Data[0].disabled = true;
                        (vm.FormSchema)[3].Data[0].visibility = false;
                        (vm.FormSchema)[1].Data[0].visibility = false
                    }
                    
                    CustomData.forEach(item => {
                        (vm.FormSchema)[1].Data[0].config.options.push({
                            value: item.value,
                            name: item.name
                        })
                    })
                }
                else{
                    vm.DependencyData=false;
                    (vm.FormSchema)[2].Data[0].disabled = true;
                    (vm.FormSchema)[3].Data[0].visibility = false;
                    (vm.FormSchema)[1].Data[0].visibility = false
                }
                if (e != "") {
                    if (e.target.value == vm.dependentId) {
                        vm.editDependency();
                    }
                }
                vm.TagsSelectedArray = [];
                vm.GetMultiSelectValue = [];
                (vm.FormSchema)[1].Data[0].value = [];
                $('#rightList input:checkbox').each(function (index, item) {
                    $(item).prop('checked', false);
                });
            })
        },
        closePopup() {
            var vm = this;
            vm.$parent.isShowDependencyFieldPopup = false;
            vm.$parent.popupcloseforsystemfields();
        },
        showWhenSelectItem(event, itm) {
            var vm = this;
            var GetDependencyDataArray = [];
            vm.checkboxdisabled = false;
            if (vm.GetEditDependencyData.value_mapping) {
                GetDependencyDataArray = JSON.parse(vm.GetEditDependencyData.value_mapping);
            }
            var ele = document.getElementsByClassName("chkItems custom-control-input");
            var toggleEle = document.getElementsByClassName("form-check-input");

            GetDependencyDataArray.forEach((item, index) => {
                if (item.value == itm.value) {
                    item.dependent.forEach(inneritem => {
                        for (var i = 0; i < ele.length; i++) {
                            if (ele[i].id == inneritem) {
                                ele[i].checked = true;
                            }
                        }
                    })
                }
            })
            vm.selectedItem = itm;
        },
        toggleSelected(event, item) { 
            var vm = this;
            let value = 0;
            if (event.target.checked == true) {
                value = item.value;
            }
            let ths = this;
            if (ths.selectedItem != null && typeof (ths.selectedItem.value) != 'undefined') {
                ths.dependentListArray.forEach(function (x) {
                    if (x.value == ths.selectedItem.value) {
                        x.isDefault = value;
                    }
                });
            }
            vm.$refs.DependencyForm.UpdateKeyValue();
        },
        checkedSelected(event, data) {
            var vm = this;
            var element = document.getElementById(data.value + '_id');
            if (event.target.checked) {
                if (element) {
                    element.disabled = false;
                }
            }
            else {
                if (element) {
                    element.disabled = true;
                    element.checked = false
                }
            }
            vm.dependentListArray.forEach((inneritem, innerindex) => {
                if (vm.selectedItem.value === inneritem.value) {
                    vm.dependentListArray[innerindex].dependent.push(data.value);
                    vm.dependentListArray[innerindex].isDefault = ""                   
                }
            })

        },
        removeRecord(event, field, meta) {
            var vm = this;
            let getindex = vm.GetMultiSelectValue.findIndex(x => x.value === event.value);
            if (getindex != -1) {
                vm.GetMultiSelectValue.splice(getindex, 1);
                vm.dependentListArray.splice(getindex, 1);
            }
        },
        GetMultiValues() {
            var vm = this;
            var obj = (vm.FormSchema)[1].Data[0].config.options.filter(x => x.name == event.target.textContent)[0];
            obj.isSel = false;
            vm.GetMultiSelectValue.push(obj);
            vm.dependentListArray.push({ value: obj.value, dependent: []});
        },
        onSubmit(formfields, e) {
            var vm = this; 
            var dependentOn = formfields.DependOn;
            if(formfields.IsRequired[0]==1){
                vm.isreq = true;
            }
            else{
                vm.isreq =false
            }
                vm.dependentListArray.push({ 
                      IsRequired: vm.isreq});
        
            var showWhenArray = [];
            var showWhen = JSON.stringify(Array.prototype.map.call(formfields.ShowWhen, function (item) {
                showWhenArray.push(item.value);
            }))
            var obj = {
                "depend_on": formfields.DependOn,
                "value": showWhenArray
            };
            var url = `customFieldDependencyId=&customlayoutId=${vm.CustomLayout_Id}&customFieldId=${vm.CustomFieldId}&dependentOn=${dependentOn}&showWhen=${JSON.stringify(obj)}&valueMapping=${JSON.stringify(vm.dependentListArray)}`
            DataService.ManageCustomFieldDependency(url).then(response => {
                if (response.data == '1') {
                    vm.SetDependField(dependentOn);
                    vm.ShowAlert(vm.$t('DependentSuccess'), "success", true, vm.$t("Alert"));
                    vm.closePopup();
                } else if (response.data == '2') {
                    vm.SetDependField(dependentOn);
                    vm.ShowAlert(vm.$t('UpdatedSuccessfully'), "success", true, vm.$t("Alert"));
                    vm.closePopup();
                }
            })
        },
        SetDependField(dependCustomField){
            var vm=this;
            vm.$parent.LayoutList.forEach(x=>{
                x.FieldData.forEach(item=>{
                    if(item.display_name==vm.displayname){
                        item.has_dependent_field=dependCustomField;
                    }
                })
            })
        },
        async editDependency() {
            var vm = this;
            vm.GetEditDependencyData = [];
            var dependentData = [];
            vm.TagsSelectedArray = [];
            vm.GetMultiSelectValue = [];
            (vm.FormSchema)[1].Data[0].value = [];
            var url = `CustomLayoutId=${vm.CustomLayout_Id}&customFieldId=${vm.$parent.CustomFieldId}&dependentOnId=`; //${vm.dependentOnDdlId}`
            await DataService.GetCustomFieldId(url).then(response => {
                dependentData = JSON.parse(response.data)
                if (dependentData.data.length > 0) {
                    vm.GetEditDependencyData = dependentData.data[0];
                    dependentData.data.forEach( xvalue =>{
                        var array = xvalue.value_mapping.split(" ");
                        array = JSON.parse(array);
                      if(array.length >= 2){
                            vm.FormSchema[2].Data[0].value= array[1].IsRequired;
                      }
                      else{
                        vm.FormSchema[2].Data[0].value=array[0].IsRequired
                      }
                       
                    })

                }
            })
            vm.dependentId = dependentData.data[0]?.dependent_on ?? null;
            if (vm.dependentId != null) {
                (vm.FormSchema)[0].Data[0].value = vm.dependentId;
                await vm.GetCustomFieldDDL('', '', vm.dependentId);
                const showWhenValues = JSON.parse(vm.GetEditDependencyData.show_when).value;
                const selectedValues = (vm.FormSchema)[1].Data[0].config.options.filter(item => {
                    return showWhenValues.includes(item.value);
                });

                vm.GetMultiSelectValue = selectedValues;
                vm.dependentListArray = JSON.parse(vm.GetEditDependencyData.value_mapping);
                vm.TagsSelectedArray = selectedValues.map(item => ({
                    label: item.name,
                    value: item.value
                }));
                (vm.FormSchema)[1].Data[0].value = vm.TagsSelectedArray;
                return;
            } else {
                return
            }

        }
    }
}
</script>

<style scoped>
.scroldv {
    max-height: 490px;
    overflow-y: scroll;
}

.sel {
    background-color: #41b883;
    /* Add your selected item style */
    color: #fff;
}

.timeline-panel li {
    max-width: 95%;
    min-width: 95%;
    margin-bottom: 0;
}

.custom-checkbox .custom-control-label {
    margin-top: 5px;
    margin-left: 0px;
    padding-top: 3px;
    padding-left: 5px;
}

.custom-control-input {
    z-index: 1;
    opacity: 0;
    top: 5px;
}

.customslidercheck .form-check-input {
    visibility: hidden;
}

.customslidercheck {
    max-width: 68px;
    width: 100%;
    position: relative;
    height: 24px;
}
</style>
